    $BackColor: #2B2B2B;
    $HeaderColor: #191919;
    $Font: 'Roboto',
    sans-serif;
    $FontColor: #EFEFEF;
    $desktop-large: 1920px;
    $tablet-landscape: 1024px;
    $tablet-portrait:768px;
    $phone-landscape: 740px;
    $phone-portrait: 414px;
    body {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        height: 100%;
        background-color: $BackColor;
    }
    
    .container {
        margin: 0 auto;
        padding: 0;
        width: 100%;
        max-width: $desktop-large;
        text-align: center;
    }
    
    .table {
        margin: 0 auto;
        padding: 0;
        width: 100%;
    }
    
    .col-1 {
        width: 8.33333333333%;
    }
    
    .col-2 {
        width: 16.6666666667%;
    }
    
    .col-3 {
        width: 25%;
    }
    
    .col-4 {
        width: 33.3333333333%;
    }
    
    .col-5 {
        width: 41.6666666667%;
    }
    
    .col-6 {
        width: 50%;
    }
    
    .col-7 {
        width: 58.3333333333%;
    }
    
    .col-8 {
        width: 66.6666666666%;
    }
    
    .col-9 {
        width: 75%;
    }
    
    .col-10 {
        width: 83.3333333333%;
    }
    
    .col-11 {
        width: 91.6666666666%;
    }
    
    .col-12 {
        width: 100%;
    }
    
    [class*="col-"] {
        float: left;
    }
    
    [class*="col-"]:last-of-type {}
    
    .row:before:after {
        clear: both;
        content: "";
    }
    
    #logo-holder {
        margin: 0 auto;
        padding: 0;
        margin-top: 8%;
        position: relative;
        left: 25%;
        text-align: center;
        .logo {
            margin: 0;
            padding: 0;
            width: 100%;
            padding-top: 15%;
        }
        a {
            font-family: $Font;
            font-size: 120%;
            text-decoration: none;
            color: $FontColor;
            position: relative;
            top: 40%;
        }
    }
    
    @media (max-width: $tablet-landscape) {
        .container {
            margin: 0 auto;
            padding: 0;
            width: 100%;
            max-width: $tablet-landscape;
            #logo-holder {
                width: 90%;
                position: relative;
                height: 200px;
                top: 5%;
                left: 5%;
                .logo {
                    margin: 0;
                    padding: 0;
                    width: 100%;
                }
            }
        }
    }
    
    @media (max-width: $tablet-portrait) {
        .container {
            margin: 0 auto;
            padding: 0;
            width: 100%;
            max-width: $tablet-portrait;
            #logo-holder {
                margin-top: 20%;
                width: 80%;
                position: relative;
                left: 10%;
            }
        }
    }
    
    @media (max-width: $phone-landscape) {
        .container {
            margin: 0 auto;
            padding: 0;
            width: 100%;
            max-width: $phone-landscape;
            #logo-holder {
                margin-top: 20%;
                width: 80%;
                position: relative;
                left: 10%;
            }
        }
    }
    /* Mobile Portrait */
    
    @media (max-width: $phone-portrait) {
        .container {
            margin: 0 auto;
            padding: 0;
            width: 100%;
            max-width: $phone-portrait;
            #logo-holder {
                margin: 0;
                padding: 0;
                margin-top: 200px;
                width: 80%;
                left: 10%;
                .logo {
                    width: 100%;
                }
            }
        }
    }